@import "definitions";

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  background-color: #fff;
  color: $color-text;
  text-rendering: optimizeLegibility;
}

h1 {
  @include h1-typeg();
}

h2 {
  @include h2-typeg();
  @include h2-spacing();
}

h3 {
  @include h3-typeg();
  @include h3-spacing();
}

h4 {
  @include h4-typeg();
  @include h4-spacing();
}

h5 {
  @include h5-typeg();
  @include h5-spacing();
}

h6 {
  @include h6-typeg();
  @include h6-spacing();
}

p {
  @include body-para();
}

.underline {
  text-decoration: underline;
}

.monospace {
  font-family: $font-monospace;
}

.small-caps {
  font-variant-caps: small-caps;
  font-feature-settings: "smcp";
}

.author-callout-style-a1 {
  color: rgb(54, 107, 251); // Blue
}

.author-callout-style-a2 {
  color: rgb(156, 39, 176); // Purple
}

.author-callout-style-a3 {
  color: rgb(213, 0, 0); // Red
}

.author-callout-style-b1 {
  background-color: rgb(144, 202, 249); // Blue
}

.author-callout-style-b2 {
  background-color: rgb(197, 225, 165); // Green
}

.author-callout-style-b3 {
  background-color: rgb(255, 183, 77); // Orange
}

.author-callout-style-b4 {
  background-color: rgb(255, 241, 118); // Yellow
}

.author-callout-style-b5 {
  background-color: rgb(158, 134, 201); // Purple
}

.author-callout-style-b6 {
  background-color: rgb(229, 115, 115); // Red
}

.author-callout-style-b7 {
  background-color: rgb(244, 143, 177); // Pink
}

.author-callout-style-b8 {
  background-color: rgb(230, 230, 230); // Grey
}

ul,
ol {
  @include margin(24, "bottom");
  margin-top: 0;
  @include padding(48, "left");
}

dl {
  @include margin(24, "bottom");
  margin-top: 0;
}

li,
dt,
dd {
  @include body-typeg();
}

dt {
  font-weight: 700;
}

dd {
  @include margin(24, "bottom");
  margin-left: 0;
}

small {
  @include small-typeg();
}

address {
  @include small-typeg();
}

.hidden {
  display: none;
}

/*
 * Hide only visually, but have it available for screen readers:
 * http://snook.ca/archives/html_and_css/hiding-content-for-accessibility
 */
.visuallyhidden {
  @include visuallyhidden();
}

/*
 * Backwards compatible clearfix.
 * See http://nicolasgallagher.com/micro-clearfix-hack/
 */
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  zoom: 1; /* ie 6/7 */
}

.global-wrapper.pull-offscreen-right {
  width: calc(100% + 330px);
}

.global-inner:after {
  content: "";
  display: block;
  clear: both;
}

img {
  max-width: 100%;
}
